h1 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
}

form {
  margin-bottom: 1rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
}

textarea {
  display: block;
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
  background-color: #b9cee3;
  
}

button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background-color: #506c81;
  color: #fff;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #1d68c3;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

p {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}
