
h1 {
  font-size: 2rem;
  text-align: center;
  margin-top: 2rem;
  color: #2f5e80;
  background-color: l;
}

div {
  margin: 0 auto;
  max-width: 800px;
  padding: 1rem;
  border: #005ea3;
  border-style: inherit;
  border-width: auto;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

label {
  margin-bottom: 0.5rem;
}

input[type='text'],
input[type='time'],
textarea {
  margin-bottom: 1rem;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  width: 100%;
}

button[type='submit'] {
  padding: 0.5rem;
  background-color: #506c81;
  color: #fff;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  width: 100%;
}

button[type='submit']:hover {
  background-color: #1d68c3;
}

p {
  margin-bottom: 0.5rem;
}

textarea {
  resize: vertical;
}

.social-media {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.social-media-card {
  width: 30%;
  margin-bottom: 1rem;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.social-media-card img {
  width: 100%;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 768px) {
  .social-media-card {
    width: 48%;
  }
}

@media screen and (max-width: 480px) {
  .social-media-card {
    width: 100%;
  }
}